@import "mixins";
@import "normalize";
@import "typography";
@import "theming";

//===================//
// Global Variables //
//==================//
$border-radius: 10px;
$max-width: 1600px;
$container-width: 90%;

* {
    scroll-behavior: smooth;
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

*:not(textarea):focus {
    // outline: 2.5px dashed var(--clr-purple)
 }    // outline-offset: 5px

body {
    &::-webkit-scrollbar {
        background-color: #000;
        @include size(0.55rem, 0.55rem); }
    &::-webkit-scrollbar-thumb {
        border: 4px solid rgba(0, 0, 0, 0);
        background-clip: padding-box;
        border-radius: $border-radius;
        background-color: #AAAAAA;
        background-color: var(--clr-pink); } }

.container {
    width: $container-width;
    max-width: $max-width;
    margin: 0 auto; }

.wide-banner {
    @include backdrop-blur();

    width: 100vw;
    position: relative;
    &::before,
    &::after {
        content: '';
        position: absolute;
        left: 0;
        @include size(100vw, 1.5px);
        background: var(--banner-border-grd); }
    &::before {
        top: 0; }
    &::after {
        bottom: 0; } }

.not-centered {
    text-align: left !important;
    * {
        text-align: left !important; } }

a {
    text-decoration: none;
    display: inline-block; }

a, button {
    cursor: pointer;
    color: var(--fg); }

button {
    background-color: transparent; }

.btn,
button,
input[type = submit],
input[type = text],
input[type = email],
textarea {
    border: none; }

textarea {
    resize: none; }

input[type = text],
input[type = email],
textarea {
    position: relative;
    display: block;
    width: 100%;
    border-width: 2px;
    border-style: solid;
    border-radius: $border-radius;
    padding: 0.5rem 0.7rem;
    outline: none; }

textarea {
    height: auto; }

.btn {
    border-radius: 6px;
    padding: 0.5rem 1.5rem;
    position: relative;
    @include flex(center, center, row, 0.25rem);
    flex-shrink: 0;
    @include btn-border-grd();
    box-shadow: 0 1px 10px rgba(240,95,135, 0.5);
    transition: background-position $btn-transition-duration, box-shadow $btn-transition-duration;
    &:hover {
        box-shadow: 0 1px 25px rgba(240,95,135, 0.5); }

    .btn-icon {
        @include size(22px, 22px);
        background-repeat: no-repeat;
        background-size: cover; } }

.btn-primary,
.btn-secondary {
    @extend .btn; }

.btn-primary {
    background-image: var(--btn-grd-pink-purple);
    background-size: 200% auto;
    background-position: 100% 50%;
    .btn-icon {
        background-image: url('../assets/arrow.svg');
        transition: transform $btn-transition-duration; }
    &:hover {
        background-position: left center;
        .btn-icon {
            transform: translateX(5px); } } }

.btn-secondary {
    background-color: var(--bg);
    flex-direction: row-reverse;
    gap: 10px; }

.header-btn {
    padding: 0.35rem 1.5rem;
    .btn-icon {
        @include size(15px, 15px); } }
