@import "generic";

.root {
    --header-height: 70px; }

html {
    @include size(100%, 100%);
    height: -webkit-fill-available;
    height: -webkit-stretch; }

body {
    @extend .dark;
    background-color: var(--bg);
    color: var(--fg);

    @include size(100%, 100%);

    @include flex(start, stretch, column);
    position: relative;
    overflow-x: hidden; }

body > header {
    position: fixed;
    @include size(inherit, var(--header-height));
    z-index: 9999;
    @include backdrop-blur();
    border-bottom: 1px solid var(--clr-nav-border);
    .container {
        display: grid;
        grid-template-columns: 1fr 3fr 1fr;
        .desktop-nav {
            grid-column: 2/3;
            @include flex(center, center, row, 2rem);
            .logo {
                @include size(70px, 70px);
                overflow: hidden;
                svg {
                    @include size(inherit, inherit); } }
            .desktop-links {
                @include flex(inherit, inherit, inherit, inherit);
                .section-link a {
                    text-transform: uppercase;
                    font-size: 90%;
                    padding: 0.25rem 0.4rem;
                    border-radius: 5px;
                    transition: background-color 0.15s ease-in;
                    &:focus {
                        background-color: var(--clr-purple); } } } }
        .header-btn {
            grid-column: 3/4;
            justify-self: end;
            align-self: center;
            width: fit-content !important; }
        #btn-ham {
            display: none;
            flex-direction: column;
            @include size(25px, 25px);
            cursor: pointer;
            background-image: url('../assets/ham-menu-closed.svg');
            background-size: cover; } }
    .mobile-nav {
        position: absolute;
        top: -100vh;
        left: 0;
        // transition: top 0.15s ease-out
        width: 100vw;
        height: calc(100vh - var(--header-height));
        background-color: var(--bg);
        padding: 2rem;
        @include flex(space-between, start, row);
        ul {
            position: relative;
            z-index: 3;
            counter-set: links-counter;
            @include flex(center, start, column, 0.3rem);
            .ham-link {
                font-size: 130%;
                text-decoration: underline;
                &:nth-child(-n+9)::before {
                    counter-increment: links-counter;
                    content: '0'counter(links-counter);

                    font-size: 70%;
                    opacity: 0.5;
                    margin-right: 10px;
                    text-decoration: none; } } }
        small {
            transform: rotate(-90deg);
            -webkit-transform: rotate(-90deg);
            -moz-transform: rotate(-90deg);
            -ms-transform: rotate(-90deg);
            -o-transform: rotate(-90deg);
            filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
            transform-origin: top right;
            opacity: 0.5; }
        .ham-img {
            position: absolute;
            @include size(120vw, 700px);
            @include max-size(650px, 700px);
            transform: translate(-50%, 0);
            left: 50%;
            bottom: -8%;
            background-image: url('../assets/fashionista.svg');
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center bottom; } } }

.menu-opened {
    #btn-ham {
        background-image: url('../assets/ham-menu-opened.svg') !important; }
    .mobile-nav {
        display: flex;
        top: var(--header-height); } }
body > main {
    @include size(inherit, 100%); }

.landing {
    min-height: 100%; }

.me, .skills, .work {
    margin-top: calc(-1 * var(--header-height));
    padding-top: calc(2* var(--header-height)); }

.work {
    margin-bottom: var(--header-height); }

.contact {
    height: 100%;
    min-height: 650px;
    padding-top: calc(1 * var(--header-height)); }

body {
    position: relative; }
/////////////////////////////////////////////////////////////////
// Redundant 2 lines (Just to prevent some SASS indentation issue)
// Remove the two line to check the issue
/////////////////////////////////////////////////////////////////
.fix {
    position: absolute; }

@import "landing";
@import "decorations";
@import "me";
@import "skills";
@import "work";
@import "contact";

h1, h2 {
    @include fill-grd(); }

.skills {
    p {
        max-width: 60ch; } }

section .title {
    display: flex;
    justify-content: start;
    align-items: center;
    grid-gap: 0.5rem;
    margin-bottom: 1rem;
    white-space: nowrap; }

@media screen and ( min-width: $laptop) { //1116px
    .img-title {
        width: 80px; } }

@media screen and ( max-width: $laptop) { //1116px
    .img-title {
        width: 70px; } }

@media screen and (max-width: $tablet0) { //950px
    body > header .container {
        grid-template-columns: 1fr auto;
        .desktop-nav {
            grid-column: 1;
            justify-content: start;
            gap: 1rem;
            .logo {
                order: 1; }
            .desktop-links {
                order: 2; } }
        .btn {
            grid-column: 2; } } }

@media screen and ( max-width: $tablet1) { //750px
    .img-title {
        width: 65px; }
    .me, .skills {
        p {
            max-width: 100%; } } }

@media screen and ( max-width: $tablet2) { //650px
    body {
        --header-height: 50px;
        header > .container {
            grid-template-columns: 1fr 2fr 1fr;
            align-items: center;
            .desktop-nav {
                grid-column: 2;
                justify-self: center;
                .logo {
                    @include size(50px, 50px); }
                .desktop-links {
                    display: none; } }
            #btn-ham {
                display: block;
                grid-column: 1;
                grid-row: 1; }
            .mobile-nav {
                background-color: var(--bg); }

            .header-btn {
                display: none; } } }
    // section:not(.landing):not(.contact)
    //     margin-top: calc(-1 * var(--header-height))
    //     padding-top: calc(2 * var(--header-height))
    // .contact
 }    //     padding-top: var(--header-height)

@media screen and ( max-width: $phone1) { //475px
    .img-title {
        width: 60px; }
    .btn {
        justify-content: center;
        .btn-icon {
            @include size(15px, 15px); } }
    .title {
        flex-wrap: wrap;
        justify-content: center !important;
        h2 {
            width: min-content; } } }
