//Global variables used here
$padding: 1rem;

.work {
    --gap: 2rem;
    .cards-container {
        @extend .not-centered;

        @include flex(start, center, row, var(--gap));

        overflow-x: auto;

        @include apply-cards-effect();

        //TODO: take card of those
        .generic-card {
            border-radius: 25px;
            background-color: var(--clr-card);
            border: 2px solid var(--clr-card-border);
            position: relative; }
        .card {
            @extend .generic-card;
            @include size(340px, 380px);
            @include flex(space-between, start, column);
            overflow: hidden;
            z-index: 2;
            *:not(.card-bg) {
                z-index: 3; }
            .card-bg {
                @include absolute-cover();

                border-radius: inherit;
                background-repeat: no-repeat;
                background-size: 70%;
                background-position: bottom -30% right -40%;

                opacity: 0.2; }
            header {
                width: 100%;
                padding: $padding $padding 0;
                @include flex(start, start, column, 0.5rem);
                .card-title {
                    @include flex(start, center, row, 0.5rem);
                    .card-name {
                        font-weight: 800;
                        line-height: 120%;
                        @include fill-grd(var(--grd-purple-green)); }
                    .arrow {
                        transition: transform .2s ease;
                        justify-self: start;
                        align-self: center;
                        @include size(22px, 22px); }
                    &:hover {
                        filter: brightness(0.8); } }
                .work-done {
                    grid-row: 2;
                    grid-column: 1/3;
                    justify-self: start;
                    @include flex(start, start, row, 0.5rem);
                    flex-grow: 1;
                    img {
                        @include size(30px, auto); } } }

            footer {
                position: relative;
                display: grid;
                grid-template-rows: repeat(2, auto);
                grid-template-columns: 1fr auto;
                grid-gap: 0.75rem 0;
                padding: $padding;
                border-radius: inherit;
                .card-description {
                    grid-column: 1/3; }
                .year {
                    grid-row: 2/3;
                    grid-column: 2/3;
                    align-self: center;
                    @include set-badge-styles(); }
                .related-links {
                    grid-row: 2/3;
                    grid-column: 1/2;
                    @include flex(start, center, row, 0.3rem);
                    a {
                        @include darken-on-hover(0.8);
                        @include size(27px,27px);
                        background-size: cover;
                        img {
                            @include size(inherit, inherit); }
                        &:nth-child(1) {
                            background-image: url(../assets/card-dribbble.svg); }
                        &:nth-child(2) {
                            background-image: url(../assets/card-github.svg); } } } }
            @media not (hover) {
                header .work-done img {
                    filter: brightness(0) invert(1); }
                footer {
                    transform: translateY(34%);
                    transition: transform .2s ease .1s; }
                &:hover {
                    .work-done img {
                        filter: brightness(1) invert(0); }
                    footer {
                        transform: translateY(0); } } } }
        #pig-dice .card-bg {
            background-image: url(../assets/pigdice.svg); }
        #monster-slayer {
            .card-bg {
                background-image: url(../assets/monster-slayer.svg); }
            .card-name {
                width: min-content; } }
        #osc-geeks .card-bg {
            background-image: url(../assets/osc-geeks.svg); }

        .dummy-card {
            margin: 0 !important;
            flex-grow: 1;
            border: 2px dashed var(--clr-card-border);
            border-radius: 25px;
            align-self: stretch;
            @include grid-center();
            img {
                @include size(100px, auto);
                opacity: 0.4; } } }
    .tasks-explained {
        margin-top: 1rem;
        @include flex(start, center, row, 15px);
        flex-wrap: wrap;
        .task {
            @include flex(center, center, row, 5px);
            img {
                @include size(20px, 20px); } } } }

@media screen and (max-width: $laptop) { //1116px
    .work .cards-wrapper .cards-container {
        .card {
            @include size(310px, 350px); }
        .dummy-card {
            display: none; } } }

@media screen and (max-width: $tablet1) { //750px
    .work .cards-wrapper .cards-container {
        --overlaying-margin: 210px;
        .card {
            @include size(330px, 350px); } } }

@media screen and (max-width: $tablet2) { //650px
    .work .cards-wrapper {
        width: 100%;
        .cards-container {
            width: inherit;
            display: grid;
            grid-template-columns: repeat(3, auto);
            scroll-snap-type: x mandatory;
            scroll-behavior: smooth;
            .card {
                @include disable-cards-effect();
                width: 70vw;
                margin: 0;
                scroll-snap-align: center;
                scroll-snap-stop: always;
                .work-done {
                    img {
                        @include size(25px, 25px); } }
                &:first-of-type {
                    margin-left: var(--gap); }
                &:last-of-type {
                    margin-right: var(--gap); } } } } }

@media screen and (max-width: $phone1) { //450px
    .work {
        --gap: 1rem;
        .cards-wrapper .cards-container .card {
            width: 80vw; }
        .tasks-explained {
            justify-content: center; } } }

@media screen and (max-width: $phone2) { //360px
    .work .cards-wrapper .cards-container .card {
        height: 300px; } }
