//scroll animations
//https://coolcssanimation.com/how-to-make-scroll-animations/

.landing {
    position: relative;
    display: grid;
    grid-template-rows: auto 1fr auto;

    > .container {
        padding-top: var(--header-height);
        grid-row: 2/3;
        align-self: center; }

    .hero {
        padding: 2.5rem 0;

        .hero-text {
            .hero-heading {
                @include flex(center, start, column);
                #qualities {
                    line-height: 1.2em;
                    height: 1.2em;
                    @include fill-grd(var(--grd-purple-green));
                    text-transform: uppercase;
                    .typed-cursor {
                        font-weight: 300;
                        animation: blink 0.7s ease infinite;
                        @keyframes blink {
                            to {
                                opacity: 0; } } } } } }
        .ctas {
            margin-top: 2rem;
            @include flex(start, center, row, 1rem);
            #btn-work .btn-icon {
                display: none; } } }

    .hobbies-wrapper {
        @extend .wide-banner;
        grid-row: 3/4; }
    .hobbies {
        @include flex(space-around, center, row, 1.5rem);
        z-index: 2;
        position: relative;
        padding: 0.6rem 1rem;
        overflow: hidden;
        .hobby {
            @extend .not-centered;
            padding: 0.5rem 1rem;
            pointer-events: none;
            a {
                @include flex(start, center, row, 0.5rem);
                .name {
                    .hobby-u, .hobby-l {
                        font-weight: 600; }
                    .hobby-l {
                        margin-top: -0.3rem;
                        opacity: 0.6; } }
                img {
                    @include size(65px, 65px); } } }
        #developer .name {
            position: relative;
            #squiggly-line {
                @include absolute-center();
                width: 100%;
                top: 115%; } }
        #designer {
            $obj-select-border: 2px solid #18A0FB;
            position: relative;
            border: $obj-select-border;
            .outline,
            .mini-sq {
                position: absolute; }
            .mini-sq {
                @include size(14px, 14px);
                background-color: #fff;
                border: $obj-select-border;
                transform: translate(-50%, -50%); }
            #upper-left {
                top: 0;
                left: 0; }
            #upper-right {
                top: 0;
                left: 100%; }
            #lower-left {
                top: 100%;
                left: 0; }
            #lower-right {
                top: 100%;
                left: 100%; } } } }

@media screen and (max-width: $laptop) { //1116px
    .landing {
        .hobbies {
            padding: 0.6rem;
            grid-gap: 0.75rem;
            .hobby {
                padding: 0.5rem;
                grid-gap: 0.75rem;
                .img-hobby {
                    @include size(60px, 60px); } } } } }

@media screen and (max-width: 810px) {
    .landing {
        .container .hero {
            .hero-text {
                text-align: center;
                .hero-heading {
                    align-items: center; } }
            .ctas {
                justify-content: center; } } } }

@media screen and (max-width: $tablet2) { //650px
    .landing {
        .hero {
            align-items: stretch;
            .hero-text {
                .hero-heading {
                    align-items: center; }
                p {
                    margin-top: 0.25rem; } }
            .ctas {
                grid-gap: 1rem; } }
        .hobbies {
            .hobby {
                border-radius: $border-radius;
                pointer-events: all;
                cursor: pointer;
                border: 2px solid rgba(255, 255, 255, 0.0);
                a {
                    @include size(100%, 100%);
                    outline: none;
                    .name {
                        display: none; }
                    .img-hobby {
                        @include size(55px, 55px); }
                    &:focus {
                        .name {
                            display: block; } } }
                &:hover {
                    border-color: rgba(255, 255, 255, 0.2); } }
            #designer {
                border-radius: 0 !important;
                .mini-sq {
                    @include size(10px, 10px); } } } } }
