@mixin center-align {
    * {
        text-align: center; } }

@mixin fill-grd($grd: var(--grd-pink-purple)) {
    background: $grd;
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
    display: table-cell; }

@mixin btn-border-grd($grd: var(--btn-grd-pink-purple-border), $border-width: 2px) {
    &::before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        border-radius: inherit;

        padding: $border-width;
        background: $grd;
        background-size: 200% 100%;
        background-position: right center;
        -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
        -webkit-mask-composite: xor;
        mask-composite: exclude;

        transition: inherit; }
    &:hover::before {
        background-position: left center; } }

@mixin apply-cards-effect() {
    --overlaying-margin: 150px;
    .card {
        transition: transform .2s ease; }
    .card:not(:first-child) {
        margin-left: calc(var(--overlaying-margin) * -1);
        box-shadow: -1px 0px 48px rgba(0, 0, 0, 0.8); }
    .card:focus-within~.card, .card:hover~.card {
        transform: translateX(calc(var(--overlaying-margin) + 50px)); } }

@mixin disable-cards-effect() { //resets all effects done above
    --overlaying-margin: 0;
    box-shadow: none;
    transition: none;
    margin-left: 0; }

@mixin set-badge-styles($base-color: var(--clr-green)) {
    @extend .tiny-small;
    background-color: $base-color;
    color: rgba(0, 0, 0, 0.8);
    padding: 0.1rem 0.2rem;
    border-radius: 5px; }

@mixin backdrop-blur($blur: 50px) {
    -webkit-backdrop-filter: blur($blur);
    backdrop-filter: blur($blur);

    @supports not (backdrop-filter: none) {
        background-color: var(--banner-non-support); } }

//Generic Mixins
@mixin min-size($min-width, $min-height) {
    min-width: $min-width;
    min-height: $min-height; }

@mixin max-size($max-width, $max-height) {
    max-width: $max-width;
    max-height: $max-height; }

@mixin size($width, $height) {
    width: $width;
    height: $height; }

@mixin absolute-center() {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

@mixin grid-center() {
    display: grid;
    place-items: center; }

@mixin flex($justify-centent: start, $align-items: start, $flex-direction: row, $grid-gap: 0rem) {
    display: flex;
    justify-content: $justify-centent;
    align-items: $align-items;
    flex-direction: $flex-direction;
    grid-gap: $grid-gap; }

@mixin absolute-cover() {
    position: absolute;
    top: 0;
    left: 0;
    @include size(100%, 100%); }

//inverts the element to B&W, and back again to it's color when hovered

@mixin darken-on-hover($darkness: 0.8) {
    @media not (hover) {
        &:hover {
            filter: brightness($darkness); } } }

$invert-filter: brightness(0) invert(1);
@mixin color-on-hover($initial-filter: $invert-filter) {
    @media not (hover) {
        filter: $initial-filter;
        &:hover {
            filter: brightness(1) invert(0); } } }
